@font-face {
    font-family: 'proza_libre';
    src: url('/assets/fonts/prozalibre-bold-webfont.woff2') format('woff2'),
         url('/assets/fonts/prozalibre-bold-webfont.woff') format('woff');
    font-weight: bold;
    font-style: normal;
}

@font-face {
    font-family: 'proza_libre';
    src: url('/assets/fonts/prozalibre-regular-webfont.woff2') format('woff2'),
         url('/assets/fonts/prozalibre-regular-webfont.woff') format('woff');
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: 'proza_libre';
    src: url('/assets/fonts/prozalibre-italic-webfont.woff2') format('woff2'),
         url('/assets/fonts/prozalibre-italic-webfont.woff') format('woff');
    font-weight: normal;
    font-style: italic;
}

:root {
    --color-1: #fefefe;
    --color-3: #486bc2;
}

html {
    font-family: "proza_libre", var(--font-stack-body);
}

html::before {
    content: "";
    display: block;
    position: absolute;
    z-index: -1001;

    width: 100%;
    height: 12em;
    top: 0;
    left: 0;
    
    background: url('/assets/images/ivanchuk-notation.svg') no-repeat;
    background-position: top center;
    background-size: cover;
}

footer {
    background: url('/assets/images/ivanchuk-notation-2.svg') no-repeat;
    background-position: top 8em center;
}

h1 {
    max-width: 15rem;
    margin-right: auto;
    margin-left: auto;
}

main {
    margin-top: 0;
}

@media (min-width: 800px) {
    figure {
        width: 40em;
        margin-left: -5em;
    }

    html::before {
        height: 12em;
    }

    footer {
        background-position: top 4em center;
    }
}